import { FC } from 'react';
import { clsx } from 'clsx';

import { LinkScope, LinkProps, LinkSize, LinkTarget, LinkMode } from './link.interface';

import { borderRadius } from './../../../tailwind/styles/border-radius';
import { focusRing } from './../../../tailwind/styles/focus';
import { DefaultIcon } from '../icon/Icon';

export const Link: FC<LinkProps> = ({
  as: Tag = 'a',
  size = LinkSize.Base,
  mode = LinkMode.Inline,
  disabled = false,
  target = LinkTarget.Self,
  label,
  ...props
}) => {
  const fontSize = size === LinkSize.Base ? 'text-zep-base' : 'text-zep-0_875';
  const disabledText = disabled ? 'group-hover:no-underline' : 'group-hover:underline';
  const Icon: FC = () =>
    mode === LinkMode.Standalone ? (
      <DefaultIcon name="ArrowLongRight" size={16} className={clsx({ 'ml-zep-0_25': label })} />
    ) : (
      <></>
    );

  return (
    <Tag
      className={clsx(
        LinkScope,
        fontSize,
        focusRing,
        borderRadius,
        'border-separate',
        'leading-zep-1_25',
        'inline-flex',
        'flex-row',
        'flex-nowrap',
        'items-center',
        'group',
        'group-active:underline group-active:decoration-2',
        'cursor-pointer',
        'underline-offset-2',
        { 'underline decoration-1 hover:decoration-2': mode === LinkMode.Inline },
        { 'disabled cursor-not-allowed': disabled },
      )}
      tabIndex={disabled ? -1 : 0}
      aria-disabled={disabled}
      target={target}
      {...props}
    >
      <span className={clsx(disabledText)}>{label}</span>
      <Icon />
    </Tag>
  );
};
